import React from 'react';
import Input from '../FormsComponents/Input';
import Button from '../FormsComponents/Button';
import useForm from '../../Hooks/useForm';
import { PASSWORD_RESET } from '../../api';
import useFetch from '../../Hooks/useFetch';
import Erro from '../Helper/Erro';
import { useNavigate } from 'react-router-dom';
import HeadHTML from '../Helper/HeadHTML';

const LoginPasswordReset = () => {

    const [login, setLogin] = React.useState('');
    const [key, setKey] = React.useState('');

    const { error, loading, request } = useFetch();
    const password = useForm('password');

    const navigate = useNavigate();//return one function()

    React.useEffect(() => {
        const params = new URLSearchParams(window.location.search);
        const key = params.get('key');
        const login = params.get('login');

        if (key) {
            setLogin(login);
            setKey(key);
        }
    }, []);

    const handleSubmit = async (event) => {
        event.preventDefault();

        if (password.validate()) {
            const bodyToPost = { login: login, key: key, password: password.value };
            const { url, options } = PASSWORD_RESET(bodyToPost);

            const { response } = await request(url, options);
            if (response.ok) {
                navigate('/login');
            }
        }
    }


    return (
        <section className="animeLeft">
            <HeadHTML title="Resetar Senha" description="Resetar Senha Dogs" />
            <h1 className='title'>Resete a Senha</h1>
            <form onSubmit={handleSubmit}>
                <Input {...password} label='Nova Senha' type='password' />
                {loading ?
                    (
                        <Button disabled>Resetando...</Button>
                    ) :
                    (
                        <Button>Enviar</Button>
                    )
                }
            </form>
            {error && <Erro error={error} />}
        </section>
    )
}

export default LoginPasswordReset;