import React from 'react';
import HeadHTML from '../Helper/HeadHTML';
import useFetch from '../../Hooks/useFetch';
import { STATS_GET } from '../../api';
import Loading from '../Helper/Loading';
import Erro from '../Helper/Erro';
//this guy bellow, contains the library Victory.
//That way, I'm using Lazy and Suspense on him
//import UserStatsGrphs from './UserStatsGrphs';
const UserStatsGrphs = React.lazy(() => import('./UserStatsGrphs'))


const UserStasts = () => {

    const { data, error, loading, request } = useFetch();

    React.useEffect(() => {
        const token = window.localStorage.getItem('token');

        const { url, options } = STATS_GET(token);
        request(url, options);

    }, [request]);


    if (loading) return <Loading />;
    if (error) return <Erro error={error} />;
    if (data && data.length > 0)
        return (
            <React.Suspense fallback={<div></div>}>
                <HeadHTML title='Estatisticas' description="Estatisticas" />
                <UserStatsGrphs data={data} />
            </React.Suspense>
        );
    else return (
        <>
            <p>Provavelmente vocÃª ainda nÃ£o postou nenhuma foto.</p>
        </>
    );
}

export default UserStasts;