import React from 'react';
import Input from '../FormsComponents/Input'
import Button from '../FormsComponents/Button'
import useForm from '../../Hooks/useForm';
import useFetch from '../../Hooks/useFetch';
import { PASSWORD_LOST } from '../../api';
import Erro from '../Helper/Erro';
import { Link } from 'react-router-dom';
import HeadHTML from '../Helper/HeadHTML';


const linkStyle = {
    color: 'black',
    display: 'block',
    background: '#fb1',
    width: '180px',
    textAlign: 'center',
    margin: '10px 0',
    padding: '15px',
    borderRadius: '4px'
}

const LoginPasswordLost = () => {

    const login = useForm();
    const { data, loading, error, request } = useFetch();

    const handleSubmit = async (event) => {
        event.preventDefault();
        if (login.validate()) {
            const { url, options } = PASSWORD_LOST({ login: login.value, url: window.location.href.replace('perdeu', 'resetar') });
            request(url, options);
        }
    }

    return (
        <section className="animeLeft">
            <HeadHTML title="Perdeu Senha" description="Recuperar Senha Dogs" />
            <h1 className='title'>Perdeu a senha?</h1>

            {data ?
                (
                    <div style={{ color: '#4c1' }}>
                        A AplicaÃ§Ã£o estÃ¡ utilizando apenas Api de teste, caso tenha perdido a senha, faÃ§a outro Cadastro!
                        <Link to='/login/criar' style={linkStyle}>Criar Nova Contar</Link>
                    </div>
                ) :
                (
                    <form onSubmit={handleSubmit}>
                        <Input {...login} label="Email / UsuÃ¡rio" type='text' name='login' />
                        {loading ? (
                            <Button disabled>Enviaandoo...</Button>
                        ) :
                            <Button>Enviar Email</Button>
                        }
                    </form>
                )}


            {error && <Erro error={error} />}
        </section>
    )
}

export default LoginPasswordLost;
