import React from 'react';
import FeedModal from './FeedModal';
import FeedPhotos from './FeedPhotos';
import PropTypes from 'prop-types';

const Feed = ({ user }) => {
    const [modalPhoto, setModalPhoto] = React.useState(null);
    const [pages, setPages] = React.useState([1]);
    const [infinite, setInfinite] = React.useState(true);


    React.useEffect(() => {
        let wait = false;

        function infinitiScroll() {
            if (infinite === true) {
                const scroll = window.scrollY;
                const height = document.body.offsetHeight - window.innerHeight;
                if (scroll > height * 0.75 && !wait) {
                    setPages((pages) => [...pages, pages.length + 1]);
                    wait = true;

                    setTimeout(() => {
                        wait = false
                    }, 500);
                };
            }
        }

        window.addEventListener('wheel', infinitiScroll);
        window.addEventListener('scroll', infinitiScroll);

        return (() => {
            window.removeEventListener('wheel', infinitiScroll);
            window.removeEventListener('scroll', infinitiScroll);
        })
    }, [infinite]);

    return (
        <div>
            {modalPhoto && <FeedModal photo={modalPhoto} setModalPhoto={setModalPhoto} />}

            {pages.map((item, index) => {
                return (
                    <FeedPhotos
                        key={item + index}
                        page={item}
                        user={user}
                        setModalPhoto={setModalPhoto}
                        setInfinite={setInfinite}
                    />
                )
            })}
        </div>
    )
}

Feed.defaultProps = {
    user: 0,
}

Feed.propTypes = {
    user: PropTypes.oneOfType([PropTypes.string.isRequired, PropTypes.number.isRequired]),
}

export default Feed;