import React from 'react';
import Input from '../FormsComponents/Input';
import Button from '../FormsComponents/Button';
import useForm from '../../Hooks/useForm'
import { USER_POST } from '../../api';
import { UseContext } from '../../UserContext';
import useFetch from '../../Hooks/useFetch';
import Erro from '../Helper/Erro';
import HeadHTML from '../Helper/HeadHTML';


const LoginCreate = () => {


    const username = useForm();
    const email = useForm('email');
    const password = useForm('password');

    const { userLogin } = React.useContext(UseContext);
    const { loading, error, request } = useFetch();

    const handleSubmit = async (event) => {
        event.preventDefault();
        if (username.validate() && email.validate() && password.validate()) {
            const data = {
                username: username.value,
                email: email.value,
                password: password.value,
            }
            const { url, options } = USER_POST(data);

            const { response } = await request(url, options);
            if (response.ok) {
                userLogin(username.value, password.value);
            }
        }
    }

    return (
        <section className={'animeLeft'}>
            <HeadHTML title="Cadastrar" description="Crie sua Conta no Dogs" />

            <h1 className='title'>Cadastre-se</h1>
            <form onSubmit={handleSubmit}>
                <Input {...username} label='UsuÃ¡rio' type='Text' name='username' />
                <Input {...email} label='Email' type='email' name='email' />
                <Input {...password} label='Senha' type='password' name='password' />
                {loading ? (
                    <Button disabled>Cadastrando...</Button>) :
                    (<Button>Cadastrar</Button>
                    )}
                {error && <Erro error={error} />}
            </form>
        </section>
    )
}

export default LoginCreate;