import React from 'react';
import Input from '../FormsComponents/Input';
import Button from '../FormsComponents/Button';

import styles from './UserPhotoPost.module.css';
import useForm from '../../Hooks/useForm';
import useFetch from '../../Hooks/useFetch';
import { PHOTO_POST } from '../../api';
import Erro from '../Helper/Erro';
import { useNavigate } from 'react-router-dom';

import HeadHTML from '../Helper/HeadHTML';

const UserPhotoPost = () => {

    const nome = useForm();
    const peso = useForm('number');
    const idade = useForm('number');
    const [img, setImg] = React.useState('');

    const { data, error, loading, request } = useFetch();
    const navigate = useNavigate();

    React.useEffect(() => {
        if (data)
            navigate('/conta');
    }, [data, navigate]);

    const handleSubimit = (event) => {
        event.preventDefault();
        if (nome.validate() && peso.validate() && idade.validate()) {
            const formData = new FormData();
            formData.append('img', img.raw);
            formData.append('nome', nome.value);
            formData.append('peso', peso.value);
            formData.append('idade', idade.value);
            const token = window.localStorage.getItem('token');
            const { url, options } = PHOTO_POST(formData, token);
            request(url, options);
        }
    }

    const imgHandleChange = ({ target }) => {
        setImg({
            preview: URL.createObjectURL(target.files[0]),
            raw: target.files[0],
        });
    }

    return (
        <section className={`${styles.photoPost} animeLeft`}>
            <HeadHTML title='Poste Sua Foto' description="Postando Fotos" />

            <form onSubmit={handleSubimit}>
                <Input {...nome} label='Nome' type='text' name='nome' />
                <Input {...peso} label='Peso' type='number' name='peso' />
                <Input {...idade} label='Idade' type='number' name='idade' />

                <input className={styles.file} type='file' name='img' id='img' onChange={imgHandleChange} />

                {loading ? <Button disabled>Enviando...</Button> : <Button>Adicionar</Button>}
                <Erro error={error} />
            </form>
            <div>
                {img.preview && (
                    <div className={styles.preview}
                        style={{ backgroundImage: `url('${img.preview}')` }}
                    ></div>
                )}
            </div>
        </section>
    );
}

export default UserPhotoPost;