import React from 'react';
import { PHOTO_GET_JUST_ONE } from '../../api';
import useFetch from '../../Hooks/useFetch';
import Erro from '../Helper/Erro';
import styles from './FeedModal.module.css';
import Loading from '../Helper/Loading';
import PhotoContent from '../Photo/PhotoContent';


const FeedModal = ({ photo, setModalPhoto }) => {

    const { data, error, loading, request } = useFetch();

    React.useEffect(() => {
        async function theFunc() {
            const { url, options } = PHOTO_GET_JUST_ONE(photo.id);
            request(url, options);
        }
        theFunc();

        //Killed Modal ToESCAPE    
        const body = document.body;
        const toKeyDown = function ({ key }) {
            if (key === 'Escape') {
                setModalPhoto(null);
            }
        }
        function closeToESC() {
            body.addEventListener('keydown', toKeyDown);
        }
        closeToESC();

        return (() => {
            body.removeEventListener('keydown', toKeyDown);
        })
    }, [photo, request, setModalPhoto]);

    const handleOutsideClick = (event) => {
        const { target, currentTarget } = event;
        if (target === currentTarget)
            setModalPhoto(null)
    }

    return (
        <div className={styles.modal} onClick={handleOutsideClick}>
            {error && <Erro error={error} />}
            {loading && <Loading />}
            {data && <PhotoContent data={data} />}
        </div>
    )
}

export default FeedModal;