import React from 'react';
import { Link } from 'react-router-dom';
import styles from './PhotoContent.module.css';
import PhotoComments from './PhotoComments';
import { UseContext } from '../../UserContext';
import PhotoDelete from './PhotoDelete';
import Image from '../Helper/Image';
const PhotoContent = ({ data, single }) => {

    const user = React.useContext(UseContext);
    const { photo, comments } = data;

    return (
        <div className={`${styles.photo} ${single ? styles.single : ''}`}>
            <div className={styles.img}>
                <Image src={photo.src} alt={photo.title} />
            </div>

            <div className={styles.details}>
                <div>
                    <p className={styles.author}>

                        {user.data &&
                            user.data.username === photo.author ? <PhotoDelete id={photo.id} /> :
                            <Link to={`/perfil/${photo.author}`}>
                                @{photo.author}
                            </Link>
                        }

                        <span className={styles.vizualizacoes}>{photo.acessos}</span>
                    </p>

                    {single ? (
                        <h1 className='title'>
                            {photo.title}
                        </h1>
                    )
                        :
                        (
                            <h1>
                                <Link to={`/foto/${photo.id}`} className={styles.h2Link}>
                                    {photo.title}
                                </Link>
                            </h1>
                        )
                    }


                    <ul className={styles.attributes}>
                        <li>{photo.peso} kg</li>
                        <li>{(+photo.idade === 1) ? `${photo.idade} ano` : `${photo.idade} anos`}</li>
                    </ul>
                </div>
            </div>
            <PhotoComments single={single} id={photo.id} comments={comments} />
        </div>
    );
}

export default PhotoContent;