import React from 'react';
import { Route, Routes } from 'react-router-dom';
import UserHeader from './UserHeader';
import Feed from '../Feed/Feed';
import UserPhotoPost from './UserPhotoPost';
import UserStasts from './UserStasts';
import { UseContext } from '../../UserContext';
import NotFound from '../NotFound';
import HeadHTML from '../Helper/HeadHTML';
const User = () => {

    const { data } = React.useContext(UseContext);

    return (
        <section className='container'>
            <HeadHTML title='Minha Conta' description="Postando Fotos" />

            <UserHeader />
            <Routes>
                <Route path="/" element={<Feed user={data.id} />} />
                <Route path="postar" element={<UserPhotoPost />} />
                <Route path="estatisticas" element={<UserStasts />} />
                <Route path="*" element={<NotFound />} />
            </Routes>
        </section>
    );
}

export default User;