import React from 'react';
import FeedPhotosItem from './FeedPhotosItem';
import useFetch from '../../Hooks/useFetch';
import { PHOTOS_GET } from '../../api';
import Erro from '../Helper/Erro';
import Loading from '../Helper/Loading';
import styles from './FeedPhotos.module.css';


const FeedPhotos = ({ setModalPhoto, user, page, setInfinite }) => {
    const { data, loading, error, request } = useFetch();

    React.useEffect(() => {
        const total = 6;
        async function fetchPhotos() {
            const { url, options } = PHOTOS_GET({ page: page, total: total, user: user });
            const { response, json } = await request(url, options);

            if (response && response.ok && json.length < total) {
                setInfinite(false);
            }//Here, I'm killing the InfiniteScroll
        }
        fetchPhotos();
    }, [request, user, page, setInfinite]);

    if (error) return <Erro error={error} />
    if (loading) return <Loading />
    if (data && data.length > 0) return (
        <ul className={`animeLeft ${styles.feed}`}>
            {data.map((foto, index) => {
                return (

                    <FeedPhotosItem setModalPhoto={setModalPhoto} photo={foto} key={foto.id} />
                )
            })}
        </ul>
    )
    else return null;
}

export default FeedPhotos;