import React from 'react';
import styles from './UserHeaderNav.module.css';

import { NavLink, useLocation } from 'react-router-dom';
import { UseContext } from '../../UserContext';

import { ReactComponent as MinhasFotos } from '../../Assets/feed.svg'
import { ReactComponent as Estatisticas } from '../../Assets/estatisticas.svg';
import { ReactComponent as AdicionarFoto } from '../../Assets/adicionar.svg';
import { ReactComponent as Sair } from '../../Assets/sair.svg';
import useMedia from '../../Hooks/useMedia';

const UserHeaderNav = () => {

    const { userLogout } = React.useContext(UseContext);
    const mobile = useMedia('(max-width: 40rem)');
    const [mobileOpen, setMobileOpen] = React.useState(false);


    const { pathname } = useLocation();

    React.useEffect(() => {
        setMobileOpen(false);
    }, [pathname]);

    return (
        <>
            {mobile &&
                <button className={`${styles.btnMobile} ${mobileOpen && styles.btnMobileActive}`} onClick={() => setMobileOpen(!mobileOpen)} aria-label="Menu"></button>
            }
            <nav className={`${mobile ? styles.navMobile : styles.nav} ${mobileOpen && styles.navMobileActive}`}>

                <NavLink to='/conta' activeClassName={styles.active} end>
                    <MinhasFotos />
                    {mobile && 'Minhas Fotos'}
                </NavLink>
                <NavLink to='/conta/estatisticas' activeClassName={styles.active}>
                    <Estatisticas />
                    {mobile && 'Estatisticas'}
                </NavLink>
                <NavLink to='/conta/postar' activeClassName={styles.active}>
                    <AdicionarFoto />
                    {mobile && 'Adicionar Foto'}
                </NavLink>
                <button onClick={() => userLogout()}>
                    <Sair />
                    {mobile && 'Sair'}
                </button>
            </nav>
        </>
    )
}

export default UserHeaderNav;