import React from 'react';
import PhotoCommentsForm from '../Photo/PhotoCommentsForm';
import { UseContext } from '../../UserContext';
import styles from '../Photo/PhotoComments.module.css'
const PhotoComments = (props) => {

    const [comments, setComments] = React.useState(() => {
        return props.comments;
    });
    const { login } = React.useContext(UseContext);

    const commentsSection = React.useRef(null);

    React.useEffect(() => {
        const heightElement = (commentsSection.current.scrollHeight);
        commentsSection.current.scrollTop = heightElement;
    }, [comments]);

    return (
        <>
            <ul
                ref={commentsSection}
                className={`${styles.comments} ${(props.single) ? styles.single : ''}`}
            >
                {(comments.length > 0) &&
                    <div className={styles.wraperComents}>
                        {
                            comments.map((comment, index) => (
                                <li key={comment.comment_ID + index}>
                                    <b>{comment.comment_author}: </b>
                                    <span>{comment.comment_content}</span>
                                </li>
                            ))
                        }
                    </div>
                }
            </ul>
            {login && <PhotoCommentsForm single={props.single} setComments={setComments} id={props.id} />}
        </>
    )
}

export default PhotoComments;