import React from 'react';
import Button from '../FormsComponents/Button'
import Input from '../FormsComponents/Input';
import Erro from '../Helper/Erro';

import { Link } from 'react-router-dom';
import useForm from '../../Hooks/useForm';
import { UseContext } from '../../UserContext';

import styles from './LoginForm.module.css';
import stylesBtn from '../FormsComponents/Button.module.css';
import HeadHTML from '../Helper/HeadHTML';


const LoginForm = () => {

    const username = useForm();
    const password = useForm();

    const { userLogin, loading, error } = React.useContext(UseContext);

    const handleLogin = async (event) => {
        event.preventDefault();
        if (username.validate() && password.validate()) {
            userLogin(username.value, password.value);
        }
    }

    return (
        <section className='animeLeft'>
            <HeadHTML title="Login" description="FaÃ§a seu Login" />
            <h1 className='title'>Login</h1>
            <form className={styles.form} onSubmit={handleLogin}>
                <Input {...username} label='UsuÃ¡rio' type='text' name='username' />
                <Input {...password} label='Senha' type='password' name='password' />
                {loading ? <Button disabled>Carregando...</Button> : <Button>Entrar</Button>}
                <Erro error={error && 'Dados Incorretos!'} />
            </form>
            <Link className={styles.perdeu} to='/login/perdeu'>Perdeu a senha</Link>

            <div className={styles.cadastro}>
                <h2 className={styles.subtitle}>Cadastre-se</h2>
                <p>Ainda nÃ£o possui conta? Cadastre-se no site.</p>
                <Link className={stylesBtn.button} to='/login/criar'>Cadastro</Link>
            </div>
        </section>
    )
}

export default LoginForm;