import React from 'react';
import './App.css';

import Header from './Components/Header';
import Footer from './Components/Footer'
import Home from './Components/Home';
import Login from './Components/Login/Login';

import { BrowserRouter, Route, Routes } from 'react-router-dom';
import { UseStorage } from './UserContext';
import User from './Components/User/User';

import ProtectedConta from './Components/Helper/ProtectedConta';
import ProtectedLogin from './Components/Helper/ProtectesLogin';
import Photo from './Components/Photo/Photo';
import UserProfile from './Components/User/UserProfile';
import NotFound from './Components/NotFound';

const App = () => {
  console.log('%c Welcome to Dogs', 'background:black;color:#fb1;padding:4px;padding-right:10px;border-radius:4px;')

  return (
    <section className="App">
      <BrowserRouter>
        <UseStorage>
          <Header />
          <main className="AppBody">
            <Routes>
              <Route path="/" element={<Home />} />
              <Route path="/meuapp" element={<Home />} />
              <ProtectedLogin path='login/*' element={<Login />} />
              <ProtectedConta path='conta/*' element={<User />} />
              <Route path='foto/:id' element={<Photo />} />
              <Route path='perfil/:user' element={<UserProfile />} />
              <Route path="*" element={<NotFound />} />
            </Routes>
          </main>
          <Footer />
        </UseStorage>

      </BrowserRouter>
    </section>
  );
}

export default App;














