import React from 'react';
import styles from './PhotoDelete.module.css';
import { PHOTO_DELETE } from '../../api';
import useFetch from '../../Hooks/useFetch';
import {useNavigate } from 'react-router-dom';

const PhotoDelete = ({ id }) => {

    const { loading, request } = useFetch();
    const navigate = useNavigate();
    const handleClick = async ({ target }) => {
        const areYouSure = window.confirm('Tem certeza que deseja Deletar?');
        if (areYouSure) {
            const token = window.localStorage.getItem('token');
            if (token) {
                const { url, options } = PHOTO_DELETE(id, token);
                const { response } = await request(url, options);
                //if (response.ok) { window.location.reload() }
                if (response.ok) { navigate('/meuapp') }
            }
        }
    }


    return (
        <>
            {loading ?
                <button disabled className={styles.delete} onClick={handleClick}>Deletaando...</button> :
                <button className={styles.delete} onClick={handleClick}>Deletar</button>
            }

        </>
    );
}

export default PhotoDelete;