import React from 'react';


const regexEmail = /^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i;
const regexPass = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])[0-9a-zA-Z]{8,}$/;
const regexNumber = /\d+$/;
const validation = {
    email: {
        regex: regexEmail,
        menssage: 'Preencha um email vÃ¡lido'
    },
    password: {
        regex: regexPass,
        menssage: 'Senha deve conter, pelo menos um dÃ­gito, pelo menos uma letra maiÃºscula e pelo menos uma minÃºscula e no mÃ­nimo 8 caracters.'
    },
    number: {
        regex: regexNumber,
        menssage: 'Esse campo deve conter apenas nÃºmeros.',
    }
}

const useForm = (type) => {
    const [value, setValue] = React.useState('');
    const [error, setError] = React.useState(null);

    const validate = (value) => {
        if (type === false) return true;//No need to validate
        if (value.length === 0) {
            setError('Campo vazio, preencha um valor')
            return false;
        } else if (validation[type] && !validation[type].regex.test(value)) {
            setError(validation[type].menssage);
            return false;
        } else {
            setError(null);
            return true;
        }
    }

    const onChange = ({ target }) => {
        if (error) {
            validate(target.value)
        }
        setValue(target.value)
    }

    return {
        value,
        setValue,
        onChange,
        error,
        validate: () => validate(value),
        onBlur: () => validate(value)
    }
}

export default useForm;