import React from 'react';
import style from './Input.module.css';

const Input = ({ label, type, name, value, onChange, setValue, onBlur, validate, error }) => {
    return (
        <div className={style.wrapper} >
            <label className={style.label} htmlFor={name}>{label}</label>

            <input onBlur={onBlur} value={value} onChange={onChange} id={name} name={name} type={type} className={style.input} />

            {error && (
                <p className={style.error} >{error}</p>
            )}

        </div>
    )
}


export default Input;