import React from 'react';
import { COMMENT_POST } from '../../api';
import { ReactComponent as Enviar } from '../../Assets/enviar.svg'
import useFetch from '../../Hooks/useFetch';
import Erro from '../Helper/Erro';

import styles from '../Photo/PhotoCommentsForm.module.css';

const PhotoCommentsForm = ({ id, setComments, single }) => {
    const token = window.localStorage.getItem('token');

    const [comment, setCommentForm] = React.useState('');
    const { request, error } = useFetch();

    const handleSubmit = async (event) => {
        event.preventDefault();

        const { url, options } = COMMENT_POST(id, { comment }, token);
        const { response, json } = await request(url, options);

        if (response.ok) {
            setCommentForm('');//clearning textarea field.
            setComments((comments) => [...comments, json]);
        }
    }

    return (
        <form className={`${styles.form} ${single ? styles.single : ''}`} onSubmit={handleSubmit}>
            <textarea
                className={styles.textarea}
                value={comment}
                onChange={({ target }) => setCommentForm(target.value)}
                id="comment"
                placeholder="Comente..."
            ></textarea>
            {comment ?
                (<button className={styles.btnSend} ><Enviar /></button>) :
                (<button className={styles.btnSend} disabled><Enviar /></button>)
            }
            {error && <Erro error={error} />}
        </form>
    )
}

export default PhotoCommentsForm;